const app = getApp({allowDefault: true})
var QQMapWX = require('@/libs/qqmap-wx-jssdk.min.js')
var qqmapsdk = new QQMapWX({
	// key: 'LXCBZ-NNIKD-UZ64F-H6AFI-UNJLH-OCFGE'
		key:'ZURBZ-WWTL6-ZNDSL-M7YHC-L67Q3-HRB7V'
})
export default {
	data() {
		return {
			list: [],
			city:"泉州市",
			pageStatus:false
		}
	},
	created() {
		uni.setNavigationBarTitle({
			title: '搜索地址'
		})
	},
	onShow() {
		uni.getLocation({ //获取当前定位
			type: 'gcj02',
			geocode:true,
			success: function(res) {
				this.city=res.altitude.city;
			}
		});
	},
	methods: {
		searchList(n) {
			let that = this
			qqmapsdk.getSuggestion({
				keyword:   n,
				region:that.city,
				region_fix:0,
				success: res => {
					console.log(res)
					that.setData({
						list: res.data
					})
				},
				fail: err => {
					console.log(err)
				}
			})
		},
		bindInput(e) {
			let val = e.detail.value
			if(val!=""){
				this.searchList(val)
			}
		},
		bindConfirm(e) {
			let val = e.detail.value
			if(val!=""){
				this.searchList(val)
			}
		},
		address(item) {
			console.log(item);
			let pages = getCurrentPages()
			let prePages = pages[pages.length - 2].$vm

			console.log(prePages);
			let list = prePages.list
			let map = prePages.map
			map.longitude = item.location.lng
			map.latitude = item.location.lat
			prePages.address = item
			prePages.list = list
			prePages.map = map
			prePages.position = {
				longitude: item.location.lng,
				latitude: item.location.lat
			}
			prePages.checked = 0
			uni.navigateBack({
				delta: 1
			})
		},
		setData(obj) {
			Object.assign(this, obj)
		},
	}
}
